.card {
  background-color: #fff;
  border-radius: 10px;
  width: 200px;
  height: 250px;
  margin: 20px 10px;
  padding: 15px 20px;
  text-align: center;
  box-shadow: 0 5px 20px 2px rgba(0, 0, 0, 0.084);
  cursor: pointer;
  transition: 0.3s all;
  &:hover {
    box-shadow: 0 15px 25px 5px rgba(0, 0, 0, 0.084);
    transform: translateY(-10px);
  }
}

.title {
  font-size: 60px;
  text-align: center;
  margin: 50px auto 20px;
  color: rgb(1, 203, 190);
}

.filter {
  width: 350px;
  height: 35px;
  padding: 0 15px;
  border: none;
  border-radius: 5px;
  display: block;
  margin: 55px auto 20px;
  color: #999;
  font-size: 20px;
  outline: none;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 700px;
  margin: 50px auto;
}
